body {
  margin: 0;
  padding: 0;
  font-family: 'Space Mono', monospace;
  text-align: center;
  color: gold;
  background-color: black
}
.game-container {
  padding: 5rem;
  background: -webkit-gradient(linear, left top, left bottom, from(#fceabb), to(#f8b500));
  background: -webkit-linear-gradient(top, #fceabb, #f8b500);
  background: -o-linear-gradient(top, #fceabb, #f8b500);
  background: linear-gradient(to bottom, #fceabb, #f8b500);
}
.game-title {
  font-weight: 500;
  letter-spacing: 0.25rem;
  padding: 0.5rem;
}
form {
  font-size: 1.5rem;
  color: gold;
  padding-top: 4rem;
  margin-top: 1rem;
}
.player {
  padding-bottom: 1rem;
}
input[type="text"] {
  background: transparent;
  border-style: dotted;
  border-color: gold;
  border-width: 1px;
  color: gold;
  font-size: 2.2rem;
  padding-top: 0.5rem;
  text-align: center;
  font-family: 'Space Mono', monospace;
  margin: 0.5rem;
}
input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}
.start-button {
  font-family: 'Space Mono', monospace;
  font-size: 1.5rem;
  background: transparent;
  color: red;
  padding-top: 3rem;
  padding-bottom: 3rem;
  border-style: none;
}
.start-button:hover {
  color: gold;
}
.logo {
  height: 7rem;
  width: 7rem;
  align: center;
  -webkit-animation-name: spin;
          animation-name: spin;
  -webkit-animation-duration: 5000ms;
          animation-duration: 5000ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear; 
}
@-webkit-keyframes spin {
    from {
        -webkit-transform:rotate(0deg);
                transform:rotate(0deg);
    }
    to {
        -webkit-transform:rotate(360deg);
                transform:rotate(360deg);
    }
}
@keyframes spin {
    from {
        -webkit-transform:rotate(0deg);
                transform:rotate(0deg);
    }
    to {
        -webkit-transform:rotate(360deg);
                transform:rotate(360deg);
    }
}
.user-stats {
  padding-top: 1rem;
  z-index: 2;
}
.stats {
  font-size: 1.25rem;
}
.user-lives {
  text-align: left;
  padding-right: 2rem;
}
.username-stats {
  text-align: center;
}
.score-stats {
  text-align: right;
  padding-left: 2rem;
}
.gameover-text {
  font-family: 'Karla', sans-serif;
  font-size: 2.3rem;
  color: red;
  font-weight: 600;
  letter-spacing: 0.5rem;
  margin-top: 5rem;
  text-shadow: 2px 2px #FF0000;
}
.gameover-image {
  height: 20rem;
}
.final-stats {
  margin-top: 5rem;
  font-size: 2rem;
  border-style: 1px dotted gold
}
.restart-button {
  font-family: 'Space Mono', monospace;
  font-size: 1.5rem;
  font-weight: 550;
  background: transparent;
  color: red;
  padding: 0;
  margin-bottom: 2rem;
  border-style: none;
}


